.start-tips {
  background: #8d2dd8;
  mix-blend-mode: normal;
  opacity: 0.9;
  border-radius: 10px;
  width: 100%;
  max-width: 750px;
  margin: 0 auto 16px;
  padding: 18px 45px 18px 16px;
  position: relative;
  em {
    display: inline-block;
    width: 13px;
    height: 13px;
  }
  p {
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    color: #ffffff;
    background: url(../images/startTip.svg) left center no-repeat;
    padding-left: 29px;
    line-height: 20px;
  }
  .close {
    position: absolute;
    width: 20px;
    height: 20px;
    background: url(../images/startTipClose.svg) center no-repeat;
    top: 50%;
    margin-top: -10px;
    right: 20px;
    cursor: pointer;
  }
  &.en {
    padding: 8px 45px 8px 16px;
  }
}
@media screen and (max-width: 800px) {
  .start-tips {
    width: calc(100% - 30px);
  }
}
@media screen and (max-width: 500px) {
  .start-tips {
    padding: 8px 45px 8px 16px;
  }
}
